// import React from 'react';
import FormInput from './FormInput';

import { Box, Grid, Stack, Switch, Typography } from '@mui/material';
import { FormControlPropsSwitchAnimate } from 'types';

import Transitions from 'ui-component/extended/Transitions';

export type IchangeHandler = {
    inputChangedHandler?: any;
};

const SwitchAnimate = ({ identifier, values, input_field, inputChangedHandler }: FormControlPropsSwitchAnimate & IchangeHandler) => {
    // const SwitchAnimate = () => {
    const animate = values[0].value_cur;

    return (
            <Stack  alignItems="center">
                <Box mb={2}>
                    <Stack direction="row" alignItems="center">
                        <Typography variant={!animate ? 'h5' : 'body1'}>NO</Typography>
                        <Switch
                            checked={animate}
                            onChange={() => inputChangedHandler(!animate, identifier)}
                            inputProps={{ 'aria-label': 'controlled' }}
                        />
                        <Typography variant={animate ? 'h5' : 'body1'}>YES</Typography>
                    </Stack>
                    <Typography variant={animate ? 'h5' : 'body1'} textAlign="center">
                        BLACKHOLE ENABLED
                    </Typography>
                </Box>
                <Transitions type="grow" in={animate} position="top-left" direction="up">
                    <Grid>
                        <FormInput {...input_field} value_cur={values[1].value_cur} inputChangedHandler={inputChangedHandler} />
                    </Grid>
                </Transitions>
            </Stack>
    );
};

export default SwitchAnimate;
