// material-ui
import { useTheme } from '@mui/material/styles';
import {
    Chip,
    Tab,
    Tabs
    //  Button
} from '@mui/material';
import { Language, StashObj } from 'types';
// import { Link } from 'react-router-dom';

// assets
import { FormattedMessage } from 'react-intl';

// tab content
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles({
    recomended_tab: {
        flexDirection: 'column',
        flexWrap: 'wrap',
        ml: '17%',
        mt: 0.5
    }
});

function a11yProps(index: number) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`
    };
}

export type UIFormStashObj = {
    values: StashObj;
    identifier: string;
    inputChangedHandler?: any;
};
export default function RecommendThreshold({ values, inputChangedHandler, identifier }: UIFormStashObj) {
    const classes = useStyles();
    const theme = useTheme();

    return (
        <>
            <Tab
                className={classes.recomended_tab}
                color={theme.palette.secondary[800]}
                label={
                    <>
                        <FormattedMessage id={Language.RECOMENDED} />
                        <div style={{ display: 'flex', flexDirection: 'row' }}>
                            <Chip
                                label={values['recommend']}
                                size="medium"
                                sx={{
                                    color: theme.palette.secondary.light,
                                    background: theme.palette.secondary.dark,
                                    ml: 1.3,
                                    mt: 1
                                }}
                            />
                            <Chip
                                onClick={() => {
                                    inputChangedHandler(values['recommend'], identifier);
                                }}
                                label="set"
                                size="medium"
                                sx={{
                                    cursor: 'pointer',
                                    color: theme.palette.primary.light,
                                    background: theme.palette.secondary.dark,
                                    ml: 1.3,
                                    mt: 1
                                }}
                            />
                        </div>
                    </>
                }
                {...a11yProps(4)}
            />
            <Tabs
                variant="scrollable"
                sx={{
                    color: theme.palette.primary.main,
                    height: 69,
                    width: '200%',
                    px: 1,
                    ml: 2.2,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'start',
                    justifyContent: 'start'
                }}
            >
                {['min', 'avg', 'max'].map((key: string, idx: number) => (
                    <Tab
                        sx={{
                            cursor: 'none',
                            color: theme.palette.grey[600],
                            height: 149,

                            ml: -2.3,
                            mt: -4.5
                        }}
                        disableRipple
                        label={
                            <>
                                {key}{' '}
                                <Chip
                                    label={values[key]}
                                    size="small"
                                    sx={{ color: theme.palette.secondary.main, background: theme.palette.secondary.light }}
                                />
                            </>
                        }
                        {...a11yProps(idx)}
                    />
                ))}
            </Tabs>
        </>
    );
}
