import React from 'react';

import {
    // Button,
    ButtonGroup,
    Grid,
    IconButton,
    Switch, Typography,
    TextField,
} from '@mui/material';

import AddIcon from '@mui/icons-material/Add';
import DeleteTwoToneIcon from '@mui/icons-material/DeleteTwoTone';
import { DefaultRootStateProps, FormControlRateLimit } from 'types';
import { IchangeHandler } from '../Input/FormInput';
import { pydict } from 'utils';

import { useSelector ,useDispatch} from 'react-redux';
import { SET_RATELIMIT, SET_RATELIMIT2 } from 'store/state/type_actions';


export interface RateLimitItem {
    port: string | number ;
    bps: any;

}
interface Props {
    row: RateLimitItem;
    add_rate_limit: any;
    index: number;
    set_func: Function;
}



const   RateLimitRow = ({ row, add_rate_limit, index, set_func }: Props) => {
    const [port, setPort] = React.useState<any>(row.port);
    const mb_set = pydict(row,'bps')
    const [mbps, setMbps] = React.useState(mb_set);
    const [comp, setComp] = React.useState<any>(null);


    const dispatch = useDispatch();

 
    React.useEffect(() => {

        setPort(row.port)
        setMbps(row.bps)
        
    }, [index, row, row.port, row.bps])
    React.useEffect(() => {

        
        const port_set = port?parseInt(port,10):''
        const bps_set =mbps? parseInt(mbps,10):''
        set_func(index, {port:port_set, bps:bps_set})
        // set_func(index, {port, bps: mbps})
    }, [port, mbps])

    React.useEffect(() => {
        
    setComp (
        <ButtonGroup key={index} id={index.toString()} style={{ margin: '.3rem' }} variant="text" color="inherit" sx={{ width: '82%', minWidth: '150px', border: '1px solid',borderBottom:'.2px soild' ,borderColor: 'grey.400' }}>



            <Grid item xs={5} style={{ margin: '.3rem' }}  >

                <TextField  id="standard-basic" variant="standard" label="PORT" inputProps={{ min: 0, style: { textAlign: 'center', left: '2rem' } }} type='number'
                    onChange={(e) => { setPort(e.target.value); 
                    }} value={port} />
            </Grid >
        

            <Grid item xs={5} style={{  margin: '.3rem' }}>

                <TextField id="standard-basic2" variant="standard" label="Mb" inputProps={{ min: 0, style: { textAlign: 'center', left: '2rem' } }} type='number'
                    onChange={(e) => { setMbps(e.target.value)}} value={mbps} />
            </Grid>
     
            <IconButton onClick={() => { 
                // rem_rate_limit(index, 'remove')
                dispatch({ type: SET_RATELIMIT2,  obj:index});
       
        }} sx={{m:.4, pl: 0.75, pr: 0.75, minWidth: '0px !important', border: '.6px solid rgba(0, 0, 0, 0.23)' }} >

                <DeleteTwoToneIcon sx={{ color: 'grey.500' }} />
            </IconButton>

        </ButtonGroup>
    );
}, [port, mbps])

    return comp
};
const row = () => {
    return {
        port: '',
        bps: '',
 
    }
}


const RateLimit = ({
    placeholder,
    value_cur,
    identifier,
    ports,
    inputChangedHandler
}: FormControlRateLimit & IchangeHandler ,
) => {
// const RateLimit = () => {
    // const [ports_, setPorts] = React.useState<any[]>(ports)
    
    const [rates, setRates] = React.useState<any>(null)
    React.useEffect(() => {
        dispatch({ type: SET_RATELIMIT, 
            obj:ports
        
        });
  
    }, [ ports ])
    const ratelimit_port = pydict(
        useSelector((state: DefaultRootStateProps) => state.context),
        'ratelimit',
        []
    );
    const dispatch = useDispatch();

    const switch_rate_limit = <Grid container xs={12} mt={1} display='flex' direction='column' alignItems="center" justifyContent="center" >
        <Switch
            checked={value_cur}
            onClick={() => {
                inputChangedHandler(!value_cur, identifier)

                if (!value_cur && ratelimit_port.length === 0) {
                    // inputChangedHandler(row(), identifier, false, 0)
                    // setPorts([row()])
        dispatch({ type: SET_RATELIMIT,  obj:[row()]});

                }
             
            }}

            inputProps={{ 'aria-label': 'controlled' }}
        />
        <Typography variant={value_cur ? 'h5' : 'body1'} textAlign='center'>RATE LIMIT ENABLED</Typography>
    </Grid>

    const change_row=(index:number, rate_limit_obj:any)=>{
        inputChangedHandler(rate_limit_obj, identifier, false, index)
        const rates_temp = [...ratelimit_port]
        rates_temp[index] = rate_limit_obj
        // setPorts(rates_temp)
        dispatch({ type: SET_RATELIMIT,  obj:rates_temp});

    }

    const add_rate_limit = (index: number, action = "add") => {
        const rates_temp = [...ratelimit_port]
        // const rates_temp1 = [...ports]
        // rates_temp1.splice(index, 1)
        
        if (action === 'add')
            rates_temp.push( row());
            // rates_temp.splice(index+1, 0, { ...row() });
            // rates_temp.splice(rates_temp.length, 0, { ...row() });
        // else
        //  rates_temp.splice(index, 1);

        // console.log(rates_temp);
        
        inputChangedHandler(rates_temp, identifier, false, -1, true, true)
        dispatch({ type: SET_RATELIMIT,  obj:rates_temp});
        
        // setPorts(rates_temp)
    }
    React.useEffect(() => {
        
        setRates(<Grid container spacing={3}  alignItems="center" justifyContent="center" style={{ margin: '.2rem' }}>
            {ratelimit_port.map((port: any, index: number) =>{
                
               return <RateLimitRow  row={port} add_rate_limit={add_rate_limit} index={index} set_func={change_row} />})}
        </Grid>)
    }, [  ratelimit_port])
    
    return (<Grid container alignContent='center' alignItems="center" justifyContent="center">

        {switch_rate_limit}
        {value_cur && rates}
        <IconButton onClick={() => { add_rate_limit(0) }}  key="one" sx={{m:.4, pl: 0.75, pr: 0.75, minWidth: '0px !important', border: '.6px solid rgba(0, 0, 0, 0.23)' }}>
                <AddIcon fontSize="inherit" />
            </IconButton>
    </Grid>
    )
}
export default RateLimit;