// material-ui
import { Typography } from '@mui/material';

// project imports
import SubCard from 'ui-component/cards/SubCard';
// import SecondaryAction from 'ui-component/cards/CardSecondaryAction';

// assets
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import CloudDownloadIcon from '@mui/icons-material/CloudDownload';
import React from 'react';

// material-ui
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import DataThresholdingIcon from '@mui/icons-material/DataThresholding';
// assets
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import { capitalize_array } from 'utils';

export interface accordionProps {

    expandIcon?: React.ReactElement;
    data: any;
    expand: boolean;
}
const get_icon = (title: string) => {
    switch (title) {
        case 'client':
            return <CloudDownloadIcon fontSize="small" color="secondary" sx={{ mr: 1.5 }} />;
        case 'xflow':
            return <CloudUploadIcon fontSize="small" color="secondary" sx={{ mr: 1.5 }} />;

        default:
            return <DataThresholdingIcon fontSize="small" color="secondary" sx={{ mr: 1.5 }} />;
    }
};
const Accordion = ({  data, expandIcon, expand }: accordionProps) => {
    const theme = useTheme();
    const open = expand? true:!data.props.input_obj.form_elements.
    every((o:any)=>o.type1==='FormControlObject'?o.form_elements.every((ob:any)=>ob.value_cur==='') :o.value_cur==='')
    
    const [expanded, setExpanded] = React.useState<boolean>(open);
    React.useEffect(() => {
        setExpanded(open)
    }, [open]);

    const handleChange = (event: React.SyntheticEvent<Element, Event>) => {
        setExpanded(!expanded);
    };
    const title = data.props.input_obj.placeholder
    return (
        <MuiAccordion expanded={expanded}  onChange={handleChange}>
            <MuiAccordionSummary
                expandIcon={expandIcon || expandIcon === false ? expandIcon : <ExpandMoreIcon />}
                sx={{ color: theme.palette.mode === 'dark' ? 'grey.500' : 'grey.800', fontWeight: 500 }}
            >
                {get_icon(title)}
                <Typography variant="subtitle1" color="secondary">
                    {capitalize_array(title.split('_'))}
                </Typography>
            </MuiAccordionSummary>
            <MuiAccordionDetails>{data}</MuiAccordionDetails>
        </MuiAccordion>
    );
};

