// =============================|| UI ACCORDION ||============================= //
export interface accordionDeviceProps {
    data_arr: any[];
    title:string;
    expand: boolean;

}

// ==============================|| ACCORDION ||============================== //

const UIAccordion = ({ data_arr , title, expand}: accordionDeviceProps) => {
    
    return (
        <SubCard title={title!== 'networks'?"DETAILS":'THRESHOLD'}
        sx={{ alignItems:'right',  minHeight:800,
        top:111,pt:-21
        // mt:-24
}}
        
        >
            <Box sx={{ width: '100%', 
                // height:'200%',
                            // minHeight:500
        
        }}>
                {data_arr.map((item: any) => (
                    <Accordion expandIcon={<ArrowDropDownIcon />}  data={item} expand={expand} />
                ))}
            </Box>
        </SubCard>
    );
};

export default UIAccordion;
