import SideForm from './SideForm';

// import { useParams } from 'react-router-dom';
import React from 'react';
import {
    object_to_values, object_to_idefinter ,
    array_to_index_with_key2,
    input_changed_handler,
    insert,
    merge_deep,
    pydict
} from 'utils';
import { get_fields, get_mikrotik_field } from '.';
import {
    FormControlPropsDropLIst,
    FormField,
    NavigationEnum,
} from 'types';
import { useDispatch } from 'react-redux';
import HocNavigate from 'common/hooks/useNavigate';
import useAuth from 'common/hooks/useAuth';

// todo improving mergeDeep
type FormParams = {
    id?: string;
    model_name: NavigationEnum;
    account_name: NavigationEnum;
};

function ContainerForm() {
    const dispatch = useDispatch();

    // const params = useParams() as FormParams;
    const hoc_navigate = HocNavigate();
    const { permissions } = useAuth();

    const { model_name, id } = hoc_navigate.params as FormParams;
    const model: NavigationEnum = model_name;
    const [fields, setFields] = React.useState<FormField[]>([]);
    const [comp, setComp] = React.useState<any>(null);
    const [error, setError] = React.useState('');

    
 
    React.useEffect(() => {
        let fields_to_set = get_fields(model_name!);
        if(model_name===NavigationEnum.ACCOUNT_USERS_TABLE && permissions.supported_users_roles )
        (fields_to_set[1] as FormControlPropsDropLIst).options = permissions.supported_users_roles 
        if(model_name===NavigationEnum.NETWORKS_TABLE && permissions.child_or_parent_has_mikrotik)
            fields_to_set = insert(fields_to_set, 1, get_mikrotik_field());
   
        if (id !== 'add' && !['attacks', 'flows'].includes(model)) {
     
                hoc_navigate.fetch_model_by_id(dispatch).then((res: any) => {
                    if (res[0]) {
                        let obj = res[1];
                        if (obj['threshold']) {
                            obj = { ...obj, ...obj['threshold'] };
                            delete obj['threshold'];
                        }
                        console.log(obj);
                         change(obj, fields_to_set);
                    }
                    else{
                        
                        setError(res[1]);
                        
                setFields(fields_to_set);

                    }
                });
            } 
            else {
                setFields(fields_to_set);

            }
        
    }, [model_name]);

    const change = (obj: any,fields_to_set:any) => {
        
        const object_idefinter = object_to_idefinter(fields_to_set);

        const object_values = object_to_values(fields_to_set);

        const faields = merge_deep(obj, object_values, object_idefinter, fields_to_set, setFields);
        Object.keys(pydict(obj, 'detailed_recommendation',{})).forEach((element) => {
            const index = array_to_index_with_key2(faields, element, 'placeholder');
            if(index>-1){
            const bps_temp = pydict(obj, `detailed_recommendation.${element}.bps`);
            if (bps_temp) faields[index].form_elements[0].recommend_values = bps_temp;
            const pps_temp = pydict(obj, `detailed_recommendation.${element}.pps`);
            if (pps_temp) faields[index].form_elements[1].recommend_values = pps_temp;
        }});
      
        setFields(faields);

    };

    React.useEffect(() => {
       
        
        fields.length>0 && setComp(
            <SideForm
                form_fields={fields}
                error_msg={error}  
                inputChangedHandler={inputChangedHandler}
                title={model_name!}
                obj_id={id as string}
            />
        );
    }, [fields]);

    const inputChangedHandler = (
        new_value: string,
        inputIdentifier: string,
        recommend_values = false,
        index = -1,
        values = false,
        rates_limit = false
    ) => {
        
        const updatedForm = input_changed_handler(new_value, inputIdentifier, [...fields], recommend_values, index, values, rates_limit, model);
        

        setFields(updatedForm);
    };
 
    // console.log(fields);
    
    return comp;
 
}

export default ContainerForm;
