import { Grid, FormControlLabel, RadioGroup, Radio } from '@mui/material';

import React, { useEffect } from 'react';
import { FormControlProps, FormControlPropsRadioButtonWithInput, 
 } from 'types';
import FormInput from './FormInput';

export type IchangeHandler = {
    inputChangedHandler?: any;
};

const FormRadioButton = ({
    placeholder,
    options,
    identifier,
    value_cur,
    values,
    input_field,
    inputChangedHandler
}: FormControlPropsRadioButtonWithInput & IchangeHandler) => {
    
    const [input_type, setInput_type] = React.useState<FormControlProps>(input_field);
    useEffect(() => {
        
        const curr_value = values[0].value_cur.toUpperCase()
        if(!input_type.captionLabel!.includes(curr_value)){
        const new_input_type = { ...input_type };
        new_input_type.captionLabel = `${new_input_type.captionLabel} ${curr_value}`;
        setInput_type(new_input_type);
    }
    }, []);
 

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        inputChangedHandler((event.target as HTMLInputElement).value, identifier);
        const new_input_type = { ...input_type };
        new_input_type.captionLabel = `${ new_input_type.captionLabel!.slice(0, -3)} ${  (event.target as HTMLInputElement).value.toUpperCase()}`
        setInput_type(new_input_type);
    };
    
    return (
        <Grid   display="flex" direction="row" >
            {/* <MUIFormControl fullWidth sx={{ display: 'flex', direction: 'row' }}  > */}
            <Grid item  mt={2}>
                <FormInput
                    {...(input_type as FormControlProps)}
                    value_cur={values[1].value_cur}
                    inputChangedHandler={inputChangedHandler}
                />
            </Grid>
            <Grid item lg={2}>
                <RadioGroup
                    aria-label="gender"
                    name="controlled-radio-buttons-group"
                    sx={{ justifyContent: 'center' }}
                    row={false}
                    value={values[0].value_cur}
                    onChange={handleChange}
                >
                    {options.map((val: string) => (
                        <FormControlLabel value={val} control={<Radio />} label={val.toUpperCase()} labelPlacement="start" />
                    ))}
                </RadioGroup>
            </Grid>

            {/* </MUIFormControl> */}
        </Grid>
    );
};

export default FormRadioButton;
