import { Grid, Button, Typography } from '@mui/material';
// import {FormInput, AutoComplete} from '../Input/index';
// import { useDispatch } from 'react-redux';
import { ADD_MODEL, EDIT_MODEL } from 'store/state/type_actions';
import MainCard from 'ui-component/cards/MainCard';
import SubCard from 'ui-component/cards/SubCard';

import { gridSpacing } from 'store/template/constant';
import {
    TypeForm,
    FormField,
    IFormControlObject,
    Language,
    NavigationEnum
} from 'types';
import { object_to_values } from 'utils/parse_input';
import { get_form, ObjForm, OpenForm, UiForm, ui_accordion_fields, ui_accordion_models } from '.';
import UIAccordion from 'views/secunity-ui/component/UIAccordion';
import { useActions } from 'store/actions';
import React from 'react';
import DraggableDialog from 'ui-component/extended/DraggableDialog';
import { makeStyles } from '@mui/styles';
import { useIntl, FormattedMessage } from 'react-intl';
import useApi from 'common/hooks/useApi';
import HocNavigate from 'common/hooks/useNavigate';

const useStyles = makeStyles({
    button: { borderRadius: '1rem' },
    typography_title: { variant: 'contained' },
    typography_value: { variant: 'subtitle2', align: 'right' },
    linearPro: { width: '80%', color: 'primary' }
});

const matrieal_style: any = {
    button: { variant: 'contained', borderRadius: '1rem', color: 'secondary' },
    grid_left: { xs: 12, spacing: 2, mr: 3, justifyContent: 'start' },
    grid_right: { xs: 12, direction: 'row', alignItems: 'top' },
    grid_right_sub: { xs: 12, direction: 'row', display: 'flex', spacing: gridSpacing, justifyContent: 'space-around' },
    grid_button: { height: 2, margin: '.1rem 1rem', padding: '1.3rem 10rem', alignContent: 'center', justifyContent: 'space-around' },
    grid_main: { xs: 12, margin: '.1rem 2%', padding: '0 7%', alignContent: 'center' },
    error_text: { color: 'error.dark' }
};


type UIForm = {
    title: NavigationEnum;
    form_fields: FormField[];
    inputChangedHandler?: any;
    error_msg:string
    obj_id: string;
};
const inputs = (fields: FormField[],inputChangedHandler:any) =>
fields.map((input_type: FormField) => {
    const inp = input_type as IFormControlObject;
    if ([TypeForm.OBJECT].includes(inp.type)) {
        return <ObjForm input_obj={inp} inputChangedHandler={inputChangedHandler} />;
    }
    if ([TypeForm.OPEN].includes(inp.type)) {
        return <OpenForm input_obj={inp} inputChangedHandler={inputChangedHandler} />;
    }
    return <UiForm input_type={input_type as FormField} inputChangedHandler={inputChangedHandler} />;
});
function SideForm({ title, form_fields, error_msg, inputChangedHandler, obj_id }: UIForm) {

    // const dispatch = useDispatch();
    const hoc_navigate = HocNavigate();
    const { submit_form } = useActions();
    const [error, setError] = React.useState(error_msg);
    const [buttons, setButtons] = React.useState<any>(null);
    const [fields, setfields] = React.useState<any>(form_fields);
    const classes = useStyles();

    const edit: boolean = typeof obj_id === 'string' && !['', 'add'].includes(obj_id);
    const getPostsApi =   useApi(submit_form)

    const send_form = async(type: string) => {
        
        const obj_to_send = object_to_values(fields);
        const ans: any = await getPostsApi.request(title, obj_id, obj_to_send);
    
        // const ans: any = await submit_form(title, obj_id, obj_to_send);
        if (ans[0]) hoc_navigate.request_navigate(`table/${title!=='attacks'?title:'active'}`);
        else setError(ans[1]);
        
    };

    const [open, setOpen] = React.useState(false);
    const [ans, setAns] = React.useState<any>(null);

    React.useEffect(() => {
        console.log('effect');
        
        setAns([
            <SubCard title={Language.INFO} sx={{ width: sizes ? title===NavigationEnum.NETWORKS_TABLE?'35%': '42%' :title===NavigationEnum.BLACKHOLE?'100%': '50%', alignItems: 'right', minHeight: 150 }}>
                <Grid container {...matrieal_style.grid_left}>
                    {inputs(fields.filter((field: FormField) => form_keys.includes(field.placeholder)), inputChangedHandler)}
                </Grid>
            </SubCard>,
           title!==NavigationEnum.BLACKHOLE && <Grid container md={sizes ?title===NavigationEnum.NETWORKS_TABLE?7: 6 : 5} {...matrieal_style.grid_right}>
                {sizes ? (
                    <UIAccordion
                        expand={!edit}
                        title={title}
                        data_arr={inputs(fields.filter((field: FormField) => ui_accordion_fields.includes(field.placeholder)), inputChangedHandler)}
                    />
                ) : (
                    <SubCard title={title === 'users' ? 'login_info' : 'details'}>
                        <Grid container {...matrieal_style.grid_right_sub}>
                            {inputs(fields.filter((field: FormField) => !form_keys.includes(field.placeholder)), inputChangedHandler)}
                        </Grid>
                    </SubCard>
                )}{' '}
            </Grid>
        ])
    }, [fields]);

    React.useEffect(() => {
        setfields(form_fields);
    }, [form_fields]);
    const add_edit_text = useIntl().formatMessage({ id: edit ? Language.EDIT : Language.ADD });
    const text = `${useIntl().formatMessage({ id: 'submit' })}  ${add_edit_text}`;
    const add_edit_title = `${add_edit_text} ${title.slice(0, -1).toLocaleUpperCase()}`;
    React.useEffect(() => {
        setButtons([
            <Button
                className={classes.button}
                {...matrieal_style.button}
                onClick={() => {
                    hoc_navigate.request_navigate(`table/${title!=='attacks'?title:'active'}`)
                }}
            >
                <FormattedMessage id="cancel_" />
            </Button>,
            <Typography {...matrieal_style.error_text}>{error}</Typography>,

            <Button
                className={classes.button}
                {...matrieal_style.button}
                onClick={() => {
                    setOpen(true);
                }}
            >
                {text}
            </Button>

        ]);
    }, [error, fields]);

    const form_keys = get_form(title);
    const sizes = ui_accordion_models.includes(title);
    // const ans = 
    return (
        <Grid container justifyContent='center'>
   
            <MainCard title={add_edit_title}>
                <Grid container {...matrieal_style.grid_button}>
                    <DraggableDialog
                        title={add_edit_title}
                        open={open}
                        func_send={send_form}
                        args={[edit ? EDIT_MODEL : ADD_MODEL]}
                        setOpen={setOpen}
                    />
                    {buttons}
                </Grid>
                <Grid container {...matrieal_style.grid_main}>
                    {ans}
                </Grid>
            </MainCard>
        </Grid>
    );
}

export default SideForm;
