// material-ui
import { useTheme } from '@mui/material/styles';
import { Autocomplete, Grid, TextField, InputLabel } from '@mui/material';

// assets
import * as React from 'react';
import useMediaQuery from '@mui/material/useMediaQuery';
import ListSubheader from '@mui/material/ListSubheader';
import { VariableSizeList, ListChildComponentProps } from 'react-window';
import Typography from '@mui/material/Typography';
import { FormControlPropsChips } from 'types';

const LISTBOX_PADDING = 8; // px

function renderRow(props: ListChildComponentProps) {
    const { data, index, style } = props;
    const dataSet = data[index];
    const inlineStyle = {
        ...style,
        top: (style.top as number) + LISTBOX_PADDING
    };

    if (dataSet.hasOwnProperty('group')) {
        return (
            <ListSubheader key={dataSet.key} component="div" style={inlineStyle}>
                {dataSet.group}
            </ListSubheader>
        );
    }

    return (
        <Typography component="li" {...dataSet[0]} noWrap style={inlineStyle}>
            {dataSet[1]}
        </Typography>
    );
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef<HTMLDivElement>((props, ref) => {
    const outerProps = React.useContext(OuterElementContext);
    return <div ref={ref} {...props} {...outerProps} />;
});

function useResetCache(data: any) {
    const ref = React.useRef<VariableSizeList>(null);
    React.useEffect(() => {
        if (ref.current != null) {
            ref.current.resetAfterIndex(0, true);
        }
    }, [data]);
    return ref;
}

// Adapter for react-window
const ListboxComponent = React.forwardRef<HTMLDivElement, React.HTMLAttributes<HTMLElement>>(function ListboxComponent(props, ref) {
    const { children, ...other } = props;
    const itemData: any[] = [];
    
    (children as React.ReactChild[]).forEach((item: React.ReactChild & { children?: React.ReactChild[] }) => {
        itemData.push(item);
        itemData.push(...(item.children || []));
    });

    // itemData = [itemData[0]]

    const theme = useTheme();
    const smUp = useMediaQuery(theme.breakpoints.up('sm'), {
        noSsr: true
    });
    const itemCount = itemData.length;
    const itemSize = smUp ? 36 : 48;

    const getChildSize = (child: React.ReactChild) => {
        if (child.hasOwnProperty('group')) {
            return 48;
        }

        return itemSize;
    };

    const getHeight = () => {
        if (itemCount > 8) {
            return 1 * itemSize;
        }
        return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
    };

    const gridRef = useResetCache(itemCount);
    
    return (
        <div ref={ref}>
            <OuterElementContext.Provider value={other}>
                <VariableSizeList
                    itemData={itemData}
                    height={getHeight() + 2 * LISTBOX_PADDING}
                    width="100%"
                    ref={gridRef}
                    outerElementType={OuterElementType}
                    innerElementType="li"
                    itemSize={(index) => getChildSize(itemData[index])}
                    overscanCount={5}
                    itemCount={itemCount}
                >
                    {renderRow}
                </VariableSizeList>
            </OuterElementContext.Provider>
        </div>
    );
});
// const OPTIONS = Array.from(new Array(969000)).map((_, i) => `${i + 1}`);
// const get_options_func = (n:number)=>  Array.from(new Array(n)).map((_, i) => `${i + 1}`);
const init_option = ['add value and click here on value']
export type IchangeHandler = {
    inputChangedHandler?: any;
};
const AutoCompleteMulitple = ({
    placeholder,
    identifier,
    value_cur,

    inputChangedHandler
}: FormControlPropsChips & IchangeHandler) => {
    const [options, setOptions] = React.useState(init_option)
    // const AutoCompleteMulitple = () => {
    const handleChange = (e: object, values: any[]) => {
        const selectedValues = values.map((item) => {
            return parseInt(item, 10);
        });
        
        inputChangedHandler(selectedValues, identifier);
    };
    
    return (
        <Grid  mb={2}>
            <InputLabel>{placeholder.toUpperCase()}</InputLabel>
            <Autocomplete
                multiple
                renderInput={(params:any) => {
                    const value_input = params.inputProps.value
                    if(typeof value_input==='string'){
                        value_input.length>0 && value_input!== options[0] && setOptions([params.inputProps.value])
                        value_input.length===0 && value_input!== init_option[0] && setOptions(init_option)

                    }
                    return <TextField {...params} type="number" />}}
                id="virtualize-demo"
                key={identifier}
                disableListWrap
                onChange={handleChange}
                defaultValue={value_cur}
                value={value_cur}
                ListboxComponent={ListboxComponent}
                options={options}
                renderOption={(props, option) => [props, option]}
                renderGroup={(params) => params}
            />
        </Grid>
    );
};

export default AutoCompleteMulitple;
