export const get_mikrotik_field = (): FormField => {
    return map_on_object([...mikrotik_input_type])[0];

}
export const get_fields = (type_form: NavigationEnum): FormField[] => {
    switch (type_form) {
        case NavigationEnum.DEVICES_TABLE:
            return map_on_object([...device_input_type]);
        case NavigationEnum.ACCOUNT_USERS_TABLE:
            return map_on_object([...user_input_type]);
        case NavigationEnum.NETWORKS_TABLE:
            return map_on_object(network_input_type);
        case NavigationEnum.ACCOUNTS_TABLE:
            return map_on_object([...account_input_type]);
        case NavigationEnum.FLOWS_TABLE:
            return map_on_object([...flow_input_type]);
        case NavigationEnum.ATTACKS_TABLE:
            return map_on_object([...flow_input_type]);
        case NavigationEnum.BLACKHOLE:
            return map_on_object([...blackhole_input_type]);
    }

    return map_on_object([...account_input_type]);
};
export const get_grid = (type: string): GridStyle => {
    let lg: GridSize = 4;
    switch (type) {
        case 'FormControlPropsDropLIst':
            lg = 6;
            break;
        case 'FormControlProps':
            lg = 6;
            break;
        case 'FormControlPropsSwitchAnimate':
            lg = 6;
            break;
        case 'FormControlPropsChips':
            lg = 12;
            break;
        case 'receive_notifications':
            lg = 5;
            return {
                lg,
                xs: lg,
                md: lg,
                mt: 2,
                ml: 5
            };
        case 'holistic':
            lg = 4;
            return {
                lg,
                xs: lg,
                md: lg,
                ml: 5
            };
        case 'FormControlPropsRadioButtonWithInput':
            lg = 5;
            break;
        case 'FormControlRateLimit':
            lg = 12;
            break;

        case 'FormControlPropsThreshold':
            lg = 6;
            break;
    }
    return {
        lg,
        xs: lg,
        md: lg
    };
};
