type UiInput = {
    input_type: FormField;
    inputChangedHandler: any;
};

export function UiForm({ input_type, inputChangedHandler }: UiInput) {
    let comp;

    switch (input_type.type1) {
        case 'FormControlPropsDropLIst':
            comp = <AutoComplete {...(input_type as FormControlPropsDropLIst)} inputChangedHandler={inputChangedHandler} />;
            break;
        case 'FormControlProps':
            comp = <FormInput {...(input_type as FormControlProps)} inputChangedHandler={inputChangedHandler} />;
            break;
        case 'FormControlPropsSwitchAnimate':
            comp = <SwitchAnimate {...(input_type as FormControlPropsSwitchAnimate)} inputChangedHandler={inputChangedHandler} />;
            break;
        case 'FormControlPropsChips':
            comp = <AutoCompleteMulitple {...(input_type as FormControlPropsChips)} inputChangedHandler={inputChangedHandler} />;
            break;
        case 'FormControlPropsCheckBox':
            const { value_cur, identifier, captionLabel } = { ...(input_type as FormControlPropsCheckBox) };
            
            return (
                <Grid item justifyContent="center" {...get_grid(input_type.placeholder)}>
                    <FormControlLabel
                        labelPlacement="top"
                        key={captionLabel}
                        control={<Checkbox value={value_cur} checked={value_cur}   onChange={() => inputChangedHandler(!value_cur, identifier)} />}
                        label={captionLabel as string}
                    />
                </Grid>
            );
        case 'FormControlPropsThreshold':
            // console.log('dsfadfs', input_type);
            
            comp = (input_type as FormControlPropsThreshold).recommend_values ? (
                <div>
                    <FormInput {...(input_type as FormControlProps)} inputChangedHandler={inputChangedHandler} />
                    <RecommendThreshold
                        identifier={(input_type as FormControlPropsThreshold).identifier}
                        values={(input_type as FormControlPropsThreshold).recommend_values as StashObj}
                        inputChangedHandler={inputChangedHandler}
                    />
                </div>
            ) : (
                <FormInput {...(input_type as FormControlProps)} inputChangedHandler={inputChangedHandler} />
            );
            break;
        case 'FormControlPropsRadioButtonWithInput':
            comp = <FormRadioButton {...(input_type as FormControlPropsRadioButtonWithInput)} inputChangedHandler={inputChangedHandler} />;
            break;
        case 'FormControlPropsRateLimit':
            // return null
            
            return <RateLimit {...(input_type as FormControlRateLimit)} inputChangedHandler={inputChangedHandler} />;
    }

    return (
        <Grid item {...get_grid(input_type.type1)}>
            {comp}
        </Grid>
    );
}

interface ObjFormProps {
    input_obj: FormControlObject;
    inputChangedHandler: any;
}
export function ObjForm({ input_obj, inputChangedHandler }: ObjFormProps) {
    return (
        <Grid item lg={12} spacing={gridSpacing}>
            <SubCard sx={{margin:-.2}} title={capitalize_array(input_obj.placeholder.split('_'))}>
                <Grid container direction="row" spacing={2}>
                    {input_obj.form_elements.map((input_type: FormField | InputField) => {
                        const inp = input_type as IFormControlObject;
                        if ([TypeForm.OBJECT].includes(inp.type)) {
                            return <ObjForm input_obj={inp} inputChangedHandler={inputChangedHandler} />;
                        }
                        return <UiForm input_type={input_type as FormField} inputChangedHandler={inputChangedHandler} />;
                    })}
                </Grid>
            </SubCard>
        </Grid>
    );
}

interface ObjFormProps {
    input_obj: FormControlObject;
    inputChangedHandler: any;
}
export function OpenForm({ input_obj, inputChangedHandler }: ObjFormProps) {
    return (
        <Grid container spacing={3}>
            {input_obj.form_elements.map((input_type: FormField | InputField) => {
                const inp = input_type as IFormControlObject;
                if ([TypeForm.OBJECT].includes(inp.type)) {
                    return <ObjForm input_obj={inp} inputChangedHandler={inputChangedHandler} />;
                }
                return <UiForm input_type={input_type as FormField} inputChangedHandler={inputChangedHandler} />;
            })}
        </Grid>
    );
}
