// material-ui
// import { useTheme } from '@mui/material/styles';
import { InputLabel, OutlinedInput } from '@mui/material';
import MUIFormControl from '@mui/material/FormControl';

// import {  useIntl} from 'react-intl';
import { FormControlProps } from 'types';


// ==============================|| FORM CONTROL ||============================== //
export type IchangeHandler = {
    inputChangedHandler?:any
}
const FormInput = ({
    captionLabel,
    placeholder,
    autoComplete,
    type,
    value_cur,
    identifier,
    inputChangedHandler  
}: FormControlProps & IchangeHandler,
) => {
    // const theme = useTheme();


// const placeholder_text = placeholder === undefined? '':  useIntl().formatMessage({id: `${placeholder}_placeholder`})
let placeholder_text = placeholder === undefined? '':  placeholder.toLocaleUpperCase()
if(captionLabel==='Bps'){
    captionLabel = 'Mb'
    placeholder_text = 'Mb'
}
identifier==='4.1' && console.log(value_cur);
identifier==='4.0' && console.log(value_cur);

    const render= <MUIFormControl fullWidth  >
    <InputLabel  >{captionLabel}</InputLabel>
    <OutlinedInput
            key={identifier}

        placeholder={placeholder_text}
        value={value_cur}
        onChange={(e:any)=> {
            // console.log(e.target.value);
            inputChangedHandler(e.target.value, identifier)
        }}
        autoComplete={autoComplete}
        type={type
        
        }
        label={captionLabel}


    />
</MUIFormControl>
    return render;
};
export default FormInput;
