// import React from 'react';

// material-ui
import { useTheme } from '@mui/material/styles';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Paper, Typography, PaperProps } from '@mui/material';

// third party
import Draggable from 'react-draggable';

// draggable
function PaperComponent(props: PaperProps) {
    return (
        <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
            <Paper {...props} />
        </Draggable>
    );
}

// ===============================|| UI DIALOG - DRAGGABLE ||=============================== //

export default function DraggableDialog({open, setOpen,func_send, args, title}:any) {
    const theme = useTheme();
    // const handleClickOpen = () => {
    //     setOpen(true);
    // };

    const handleClose = () => {

        setOpen(false);
    };

    return (
        <div>

            <Dialog open={open} disableScrollLock onClose={handleClose} PaperComponent={PaperComponent} aria-labelledby="draggable-dialog-title" sx={{justifyContent:'center'}}>
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    {title}
                </DialogTitle>
                <DialogContent >
                    <DialogContentText>
                        <Typography variant="body2" component="span">
                        are you sure you want to
                        </Typography>
                    </DialogContentText>
                    <DialogContentText>
                        <Typography textAlign='end' variant="body2" component="span">
                          {title}
                        </Typography>
                    </DialogContentText>
                </DialogContent>
                <DialogActions   sx={{ justifyContent:'center'}}>
                    <Button sx={{ color: theme.palette.error.dark }} autoFocus onClick={handleClose} color="secondary">
                        Cancel
                    </Button>
                    <Button variant="contained" size="small" onClick={()=>{
        setOpen(false);
                        
                        func_send(...args)}}>
                        Submit
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
